<template>
    <el-dialog
    width="600px"
    :visible.sync="isVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="cancel"
    title="提示"
    center
    >
    <el-alert
            :title="title"
            type="warning"
            :closable="false">
          </el-alert>
          <br>
       <el-form ref="maintenanceForm" :model="maintenanceForm" :rules="formRules" label-width="80px">
        <el-form-item label="维修报告" prop="goodTestRemark">
            <el-input
              v-model="maintenanceForm.goodTestRemark"
              placeholder="请输入维修报告,最长200字符"
              :autosize="{maxRows:3}"
              type="textarea"
              maxlength="200"
              >
            </el-input>
          </el-form-item>
       </el-form>
       <div slot="footer" class="dialog-footer">
            <el-button size="small" type="primary" @click="save">提交</el-button>
            <el-button size="small" @click="cancel">取消</el-button>
        </div>
    </el-dialog>
</template>
<script>
/**
 * QualityDealFail Component
 * ----------------------
 * Author: Calamus
 * Date: 2018.08.29
 */
// validate
import Validator from '@/utils/extendValidate/extendValidate'
// api
import { maintenanceMethodsList, qualityStatus,maintenanceUpdate} from '@/api/warehouse'

export default {
  name: 'mainForm',
  data () {
    return {
      isVisible: false,
      maintenanceForm: {
        goodRepairStatus: '',
        goodRepairs: [],
        goodTestRemark: ''
      },
      formRules: {
        // goodRepairRemark: [{validator: Validator.normalNumber, trigger: 'change'}],
      }
    }
  },
  props: {
    flag: {
      type: String
    },
    show: {
      type: Boolean,
      default: false
    },
    modifyUser: {
      type: Array
    },
    searchParams: {
      type: Object
    }
  },
  computed: {
    title () {
      return '此操作将修改维修状态为' + this.flag + '!'
    }
  },
  watch: {
    show (isShow) {
      this.isVisible = isShow
    }
  },
  methods: {
    save () {
      this.$refs.maintenanceForm.validate(isValid => {
        if (isValid) {
          // const searchParams = this.searchParams = params ;
          let params = {
            goodRepairStatus: this.flag === "已修复"?'1':this.flag === "未修复"?'2':'',
            goodRepairs: [],
            goodTestRemark: this.maintenanceForm.goodTestRemark
          }
          for (let item of this.modifyUser) {
            params.goodRepairs.push(item.goodRepairUuid)
          }
          maintenanceUpdate(params).then((res) => {
            if (!res.error) {
              this.$message({
                message: '维修状态设置成功',
                type: 'success'
              })
              this.$refs.maintenanceForm.resetFields()
              this.$emit('updateList', this.searchParams)
              this.$emit('update:show', false)
              this.isVisible = false
            }
          })
        }
      })
    },
    cancel () {
      this.$refs.maintenanceForm.resetFields()
      this.$emit('update:show', false)
      this.isVisible = false
    }
  }
}
</script>
<style scoped>
.el-form-item--mini.el-form-item, .el-form-item--small.el-form-item {
    margin-bottom: 0px;
}
.el-radio {
    padding: 7.5px;
}
</style>



